/* Game board */
.game-board {
	width: 100%;
	height: 100%;
	display: grid; /* Use grid layout to manage the positioning of board cells */
	grid-template-columns: repeat(
		auto-fill,
		minmax(1fr, 1fr)
	); /* Automatically fill columns with flexible widths */
	max-width: 100%;
	gap: 1px; /* Adds a 1px gap between grid items (cells) */
	box-sizing: border-box; /* Includes padding and borders in the element's total width and height */
	background-color: #fff;
	overflow: hidden; /* Hides any content that overflows the container */
}

.game-board.locked-element::before {
	opacity: 1;
	z-index: 2; /* Ensures the element is above other elements */
	background-color: #d3d3d3;
}
