/* Container for the sorting controls of the move history */
.moves-sort-bar {
	margin-top: 0.625rem; /* 0.625rem = 10px */
	padding: 0.625rem;
	background-color: #f9f9f9;
	border: 1px solid #ddd;
	box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.2); /* 0.25rem = 4px, 0.5rem = 8px */
}

/* Container for the navigation controls of the move history */
.moves-nav-bar {
	margin-bottom: 0.3125rem; /* 0.3125rem = 5px */
	background-color: #f9f9f9;
	border: 1px solid #ddd;
	box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.2); /* 0.25rem = 4px, 0.5rem = 8px */
}

.moves-nav-bar .btn {
	width: 100%;
	height: 1.5625rem; /* 1.5625rem = 25px */
	font-size: 1.25rem; /* 1.25rem = 20px */
}
